shiny-chat-container {
  display: flex;
  flex-direction: column;
  margin: 0 auto;
  gap: 1rem;
}
shiny-chat-container shiny-chat-messages {
  display: flex;
  flex-direction: column;
  overflow: auto;
  gap: 1rem;
}
shiny-chat-container shiny-chat-messages > * {
  display: grid;
  grid-template-columns: auto minmax(0, 1fr);
  gap: 1rem;
  /* Make badges more circular */
}
shiny-chat-container shiny-chat-messages > * .badge {
  --bs-badge-padding-y: 0.65em;
  height: fit-content;
}
shiny-chat-container shiny-chat-messages .message-content p:last-child {
  margin-bottom: 0;
}
shiny-chat-container shiny-chat-messages .message-content pre code {
  /* Fix alignment of first line in a code block */
  padding: 0;
}
shiny-chat-container shiny-chat-messages .message-content.message-user {
  /* Put message in the 2nd column  and align to the right */
  grid-column: 2;
  justify-self: end;
  padding: 0.5rem 0.75rem;
  border-radius: 26px;
  background-color: var(--bs-primary-bg-subtle, #cce5f3);
  color: var(--bs-primary-text-emphasis, #0d6efd);
}
shiny-chat-container shiny-chat-input {
  margin-top: auto;
  position: sticky;
  bottom: 0;
  padding: 0.25rem;
  background-color: var(--bs-body-bg, white);
}
shiny-chat-container shiny-chat-input .btn {
  --bs-btn-padding-x: 0.75em;
  --bs-btn-padding-y: 0.25em;
}
shiny-chat-container shiny-chat-input textarea {
  resize: none;
}

/*
  Disable the page-level pulse when the chat input is disabled
  (i.e., when a response is being generated and brought into the chat)
*/
.shiny-busy:has(shiny-chat-input[disabled])::after {
  display: none;
}

/* Code highlighting */
/* TODO: we should have this read Bootstrap CSS variables */
pre code.hljs {
  display: block;
  overflow-x: auto;
  padding: 1em;
}

code.hljs {
  padding: 3px 5px;
}

/*!
  Theme: Default
  Description: Original highlight.js style
  Author: (c) Ivan Sagalaev <maniac@softwaremaniacs.org>
  Maintainer: @highlightjs/core-team
  Website: https://highlightjs.org/
  License: see project LICENSE
  Touched: 2021
*/
/*
This is left on purpose making default.css the single file that can be lifted
as-is from the repository directly without the need for a build step

Typically this "required" baseline CSS is added by `makestuff.js` during build.
*/
pre code.hljs {
  display: block;
  overflow-x: auto;
  padding: 1em;
}

code.hljs {
  padding: 3px 5px;
}

/* end baseline CSS */
.hljs {
  background: #f3f3f3;
  color: #444;
}

/* Base color: saturation 0; */
.hljs-subst {
  /* default */
}

/* purposely ignored */
.hljs-comment {
  color: #697070;
}

.hljs-tag,
.hljs-punctuation {
  color: rgba(68, 68, 68, 0.6666666667);
}

.hljs-tag .hljs-name,
.hljs-tag .hljs-attr {
  color: #444;
}

.hljs-keyword,
.hljs-attribute,
.hljs-selector-tag,
.hljs-meta .hljs-keyword,
.hljs-doctag,
.hljs-name {
  font-weight: bold;
}

/* User color: hue: 0 */
.hljs-type,
.hljs-string,
.hljs-number,
.hljs-selector-id,
.hljs-selector-class,
.hljs-quote,
.hljs-template-tag,
.hljs-deletion {
  color: #880000;
}

.hljs-title,
.hljs-section {
  color: #880000;
  font-weight: bold;
}

.hljs-regexp,
.hljs-symbol,
.hljs-variable,
.hljs-template-variable,
.hljs-link,
.hljs-selector-attr,
.hljs-operator,
.hljs-selector-pseudo {
  color: #ab5656;
}

/* Language color: hue: 90; */
.hljs-literal {
  color: #695;
}

.hljs-built_in,
.hljs-bullet,
.hljs-code,
.hljs-addition {
  color: #397300;
}

/* Meta color: hue: 200 */
.hljs-meta {
  color: #1f7199;
}

.hljs-meta .hljs-string {
  color: #38a;
}

/* Misc effects */
.hljs-emphasis {
  font-style: italic;
}

.hljs-strong {
  font-weight: bold;
}

/*
  Styling for the code-copy button (inspired by Quarto's code-copy feature)
*/
pre:has(.code-copy-button) {
  position: relative;
}

.code-copy-button {
  position: absolute;
  top: 0;
  right: 0;
  border: 0;
  margin-top: 5px;
  margin-right: 5px;
  z-index: 3;
  background-color: transparent;
}
.code-copy-button > .bi {
  display: flex;
  gap: 0.25em;
}
.code-copy-button > .bi::after {
  content: "";
  display: block;
  height: 1rem;
  width: 1rem;
  mask-image: url('data:image/svg+xml,<svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" viewBox="0 0 16 16"><path d="M4 1.5H3a2 2 0 0 0-2 2V14a2 2 0 0 0 2 2h10a2 2 0 0 0 2-2V3.5a2 2 0 0 0-2-2h-1v1h1a1 1 0 0 1 1 1V14a1 1 0 0 1-1 1H3a1 1 0 0 1-1-1V3.5a1 1 0 0 1 1-1h1v-1z"/><path d="M9.5 1a.5.5 0 0 1 .5.5v1a.5.5 0 0 1-.5.5h-3a.5.5 0 0 1-.5-.5v-1a.5.5 0 0 1 .5-.5h3zm-3-1A1.5 1.5 0 0 0 5 1.5v1A1.5 1.5 0 0 0 6.5 4h3A1.5 1.5 0 0 0 11 2.5v-1A1.5 1.5 0 0 0 9.5 0h-3z"/></svg>');
  background-color: var(--bs-body-color, #222);
}

.code-copy-button-checked > .bi::before {
  content: "Copied!";
  font-size: 0.75em;
  vertical-align: 0.25em;
}
.code-copy-button-checked > .bi::after {
  mask-image: url('data:image/svg+xml,<svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" viewBox="0 0 16 16"><path d="M13.854 3.646a.5.5 0 0 1 0 .708l-7 7a.5.5 0 0 1-.708 0l-3.5-3.5a.5.5 0 1 1 .708-.708L6.5 10.293l6.646-6.647a.5.5 0 0 1 .708 0z"/></svg>');
  background-color: var(--bs-success, #198754);
}